.main{
    display: grid;
    grid-template-areas:
    'header'
    'maincontent';
    grid-template-rows: auto auto ;
}

.header {
    grid-area: header;
    display: flex;
    justify-content: center;
    margin:90px 0px 0px;
}
.header h3{
    font-size: 30px;
    font-family: 'Ubuntu', sans-serif;
    text-transform: uppercase;
    letter-spacing: 6px;
    margin: 40px 0;
    padding: 0;
    text-align: center;
}

.content{
    grid-area: maincontent;  
}

.mainContent{
    display: grid;
    grid-template-areas:
    'mainContentUp'
    'mainContentDown';
    grid-template-rows: auto auto ;
}

.product{
    grid-area: mainContentUp;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; 
}

.component img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 3px;
    max-width: 285px;
    max-height: 350px;
    transition: all 0.5s;
    min-height: 350px;
}
.component img:hover {
    transform: scale(1.05);
}

.image{
    overflow: hidden;
    max-width: 285px;
    max-height: 350px;  
}

.component {
    max-width: 285px;
    max-height: 350px;
    margin:10px 20px 245px;
    justify-content: center;
    align-items: center;   
}

.productId{
    display: flex;
    justify-content: center;
    text-align: center;
    margin: 20px 3px 20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 19px;
    font-weight: 540;
    height: 40px;
}

.stripe{
    grid-area: mainContentDown;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 40px;
}

.increase{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.increase p{
    font-size: 28px;
    margin-top:-8px;
}
.increase span{
    font-size: 25px;
}
.increase button{
    width: 26px;
    height: 30px;
    margin: 3 3px;
    align-items: center;
    text-align: center;
    font-size: 20px;
}

.removeFromCart{
    margin-left: 4px;
}
.removeFromCart button:hover{
    background-color: red;
    color: white;
}

a{
    color: black;
    text-decoration: none;
}
a:hover{
    color: #bf1650;
    text-decoration: none;
}

.size{
    display: flex;
    justify-content: center;

}
.size button{
    margin: 0px 3px;
    border-radius: 3px;
    background-color: white;
}
.size button:hover{
    color: #bf1650;
    background-color: rgb(230, 230, 230);
}

.chosenSize{
    display: flex;
    justify-content: center;
    margin: 10px;
    font-size: 18px;
    font-weight: 400;
}

.price{
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 19px;
    font-weight: 400;
    margin-top: 12px;
}

.noProductButton{
    display: flex;
    justify-content: center;
    width: 100%;
}

.noProduct {
    grid-area: maincontent;
}
.noProduct p{
text-align: center;
font-size: 20px;
margin: 30px;
}
.noProduct span button{
    display: flex;
    margin: 0 auto;
    margin-top: 70px;
    align-items: center;
    font-size: 20px;
    height: 85px;
    width: 165px;
    border-radius: 5px;
    letter-spacing: 1px;
    transform-style: preserve-3d;
    transform: translateZ(-25px);
    transition: transform .25s;
    font-family: 'Montserrat', sans-serif;
    
}
.noProduct span button:before,
.noProduct span button:after{
    position: absolute;
    content: "ПЕРЕЙТИ В КАТАЛОГ";
    height: 85px;
    width: 165px;
    display: flex;
    top:-20px;
    align-items: center;
    justify-content: center;
    border: 3px solid black;
    box-sizing: border-box;
    border-radius: 5px;
    
}
.noProduct span button:before{
    color: #fff;
    background: #000;
    transform: rotateY(0deg) translateZ(25px);
}
.noProduct span button:after{
    color: #000;
    transform: rotateX(90deg) translateZ(25px);
}
.noProduct span button:hover{   
    transform: translateZ(-25px) rotateX(-90deg);
}

.favorites{
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    display: flex;
    margin: -420px 240px 400px;
}

.favorite{
    z-index: 1;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 30px;
    height: 30px;
    border-radius:15px;
    background-color: white;
    
}
.favorite:active{
    color: red;
    transition: 0s;
    z-index: 1;
}
.favorite:hover{
    
    color: red;
    transition: 0s;
    z-index: 1;
}

@media screen and (max-width: 500px) {
    .noProduct span button{
        display: flex;
        margin: 0 auto;
        margin-top: 10px;
        align-items: center;
        font-size: 20px;
        height: 70px;
        width: 140px;
        border-radius: 5px;
        letter-spacing: 1px;
        transform-style: preserve-3d;
        transform: translateZ(-25px);
        transition: transform .25s;
        font-family: 'Montserrat', sans-serif;
        
    }
    .noProduct span button:before,
    .noProduct span button:after{
        position: absolute;
        content: "ПЕРЕЙТИ В КАТАЛОГ";
        height: 70px;
        width: 140px;
        display: flex;
        top:-20px;
        align-items: center;
        justify-content: center;
        border: 3px solid black;
        box-sizing: border-box;
        border-radius: 5px;
        
    }
    .noProductButton span button{
        font-size: 14px;
    }
    .favoritesandCart{
        display: block;
    }
    .favoritesandCart button{
        margin: 10px auto;
    }

    .component {
        max-width: 260px;
        max-height: 330px;    
    }

    .image{
        max-width: 260px;
        max-height: 330px;    
    }

    .increase p{
        font-size: 24px;
        margin-top:-7px;
    }
    .increase span{
        font-size: 22px;
    }
    .increase button{
        width: 26px;
        height: 30px;
        margin: 0px 3px;
        align-items: center;
        text-align: center;
        font-size: 18px;
    }
    
    .productId{
        margin: 20px 3px 8px;
    }

    .favorites{
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        display: flex;
        margin: -385px 215px 375px;
    }

    .chosenSize{
        font-size: 16px;
    }

    .header {
        margin:20px 0px 0px;
    }
    .header h3{
        font-size: 20px;
        margin: 70px 0 20px;
    }

    .increase{
        margin-bottom: 200px;
    }

    .stripe{
        margin-top: 5px;
    }
}


